import { LabeledField } from "Util/Components/Input/LabeledField";
export function StringField({ value, setter, label }) {
    return <LabeledField label={label}>
        <input type="text" value={value} onChange={(e) => setter(e.target.value)}/>
    </LabeledField>;
}
export function IntField({ value, setter, label }) {
    return <LabeledField label={label}>
        <input type="number" value={value} onChange={(e) => setter(Math.floor(e.target.valueAsNumber))}/>
    </LabeledField>;
}
export function FloatField({ value, setter, label }) {
    return <LabeledField label={label}>
        <input type="number" value={value} onChange={(e) => setter(e.target.valueAsNumber)}/>
    </LabeledField>;
}
