class SettingDerived {
    setting;
    map;
    lastVersion = -1;
    u;
    constructor(setting, map) {
        this.setting = setting;
        this.map = map;
        this.update();
    }
    update() {
        this.lastVersion = this.setting.version;
        this.u = this.map(this.setting.value);
    }
    get value() {
        if (this.setting.version !== this.lastVersion)
            this.update();
        return this.u;
    }
}
export function deriveFromSetting(setting, map) {
    return new SettingDerived(setting, map);
}
