import Styles from "./Panes.module.scss";
export function PaneView({ children }) {
    return <div className={Styles.Panes}>
        {children}
    </div>;
}
export function LeftPane({ children }) {
    return <div className={Styles.LeftPane}>
        {children}
    </div>;
}
export function LeftPaneList({ children }) {
    return <div className={Styles.List}>
        <ul>
            {children}
        </ul>
    </div>;
}
export function MainPane({ children }) {
    return <div className={Styles.MainPane}>
        {children}
    </div>;
}
