import { deriveFromSetting } from "Settings/SettingDerived";
export class BasicSatchelRegistry {
    flagId;
    constructor(flagId) {
        this.flagId = flagId;
    }
    get satchelDefinition() {
        return this.getSatchelDefinition();
    }
    get(id) {
        return undefined;
    }
}
export class SettingSatchelRegistry extends BasicSatchelRegistry {
    setting;
    map;
    constructor(setting) {
        super(setting.data.key);
        this.setting = setting;
        this.map = deriveFromSetting(setting, (t) => {
            let r = {};
            t.items.forEach((x) => {
                r[x.id] = x;
            });
            return r;
        });
    }
    get itemMap() {
        return this.map.value;
    }
    get data() {
        return this.setting.value;
    }
    get(id) {
        return this.itemMap[id];
    }
    getSatchelDefinition() {
        return this.data.satchelDefinition;
    }
}
