import { getMappedSetter } from "Util/React/update/Updater";
export function getFieldData(value, setter) {
    return {
        get: function (key, label = key) {
            let fieldValue = value[key];
            let fieldSetter = getMappedSetter(key, setter);
            return {
                value: fieldValue,
                setter: fieldSetter,
                label
            };
        }
    };
}
